.slider-top {
  .slick-slide {
    position: relative;
  }
  .slick-prev, .slick-next {
    z-index: 99;
  }
  .slick-prev {
    left: rem-calc(10);
  }
  .slick-next {
    right: rem-calc(10);
  }
  a {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 132px;
    height: 32px;
    margin: auto;
    text-align: center;
  }
}



.blue h3 {
    background: #fff;
    color: #3498db;
    font-size: 36px;
    line-height: 100px;
    margin: 10px;
    padding: 2%;
    position: relative;
    text-align: center;
}
.slick-slider {
  margin: auto;
  .items-slider {
    img {
      width: 100%;
    }
  }
}
